import React from "react";
import resume from "../assets/resume.pdf";

// COMPONENT
import { ButtonOutline, ButtonPrimary } from "./Button";

const Hero = () => {
  return (
    <>
      <section id="home" className="pt-28 lg:pt-32">
        <div className="container items-center lg:grid lg:grid-cols-2 lg:gap-10">
          <div>
            <div className="flex items-center gap-3">
              <figure className="img-box w-9 h-9 rounded-lg">
                <img
                  src="./images/avatar-1.png"
                  width={40}
                  height={40}
                  alt=""
                  className="img-cover"
                />
              </figure>

              <div className="flex items-center gap-1.5 text-slate-400 text-sm tracking-wide">
                <span className="relative w-2 h-2 rounded-full bg-emerald-400">
                  <span className="absolute inset-0 rounded-full bg-emerald-400 animate-ping"></span>
                </span>
                Available for work
              </div>
            </div>

            <h2 className="headline-1 max-w-[15ch] sm:max-w-[20ch] lg:max-w-[15ch] mt-5 mb-8 lg:mb-10">
              Creating Modern, Scalable Websites for the Future
            </h2>

            <div className="flex items-center gap-3">
              <ButtonPrimary
                href={resume}
                target="_blank"
                label="Download CV"
                icon="download"
              />
              <ButtonOutline
                href={"#about"}
                label="Scroll down"
                icon="arrow_downward"
              />
            </div>
          </div>

          <div className="hidden lg:block">
            <figure className="w-full max-w-[480px] ml-auto bg-gradient-to-t from-slate-200 via-15% via-slate-200/40 to-55% rounded-[70px] overflow-hidden">
              <img
                src="./images/hero-banner.png"
                width={656}
                height={800}
                alt=""
                className="w-full"
              />
            </figure>
          </div>
        </div>
      </section>
    </>
  );
};

export default Hero;
